.m-last-0:last-child {
  margin: 0 !important;
}

.p-last-0:last-child {
  padding: 0 !important;
}

.border-last-0:last-child {
  border: 0 !important;
}

// For hidden h1 tag
.hidden-text {
  position: absolute;
  clip: rect(1px 1px 1px 1px);
  /* for Internet Explorer */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.flipbook-main-wrapper {
  z-index: 3000;
}

.close-flipbook {
  display: none;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  z-index: 5000;
  padding: 0;
  line-height: 50px;
  font-size: 20px;
  text-align: center;
  color: #fff;
  background: 0;
  border: 0;
  cursor: pointer;
  position: fixed;
  top: 0;
  right: 0;
}
